html,body {
  margin: 0;
  padding: 0;
  font-family: 'Playfair Display SC', 'Old Standard TT', "Segoe UI","Helvetica Neue", sans-serif, -apple-system, BlinkMacSystemFont,  "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background: black;
}

#root {
  height: 100vh;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
